html {
    box-sizing: border-box;
  }
  
  *,
  *::before,
  *::after {
    box-sizing: inherit;
  }
  
  a {
    color: inherit;
    text-decoration: none;
  }
  
  img {
    max-width: 100%;
  }
  
  :root {
    --text-size: 16px;
    --text-color: #fff;
    --beige-first: #ff9900;
    --beige-second: #ffb84d;
    --beige-third: #e1670e;
    --white-color: #fff;
    --grey-light: #cacaca;
    --grey: #666;
    --dark-grey: #414141;
    --black: #000;
  }
    
  body {
    max-width: 1920px;
    margin: 0 auto;
    padding: 0;
    font-family: Arial, Helvetica, sans-serif;
    background:  rgb(231, 231, 231);
  }
  
  .body.noscroll {
    position: fixed;
    overflow-y: scroll;
    width: 100%;
  }
  
  .flex {
    display: flex;
  }
  
  /*globe*/
.list-reset {
    margin: 0;
    padding: 0;
    list-style-type: none;
}
.btn-reset {
    padding: 0;
    border: none;
    background-color: transparent;
    cursor: pointer;
}
.main__container {
    max-width: 1920px;
    padding: 0 15px;
    margin: 0 auto;
}

/*header*/
.header__box {
  display: flex;
  flex-direction: row;
  align-items: center;
  max-width: 1920px;
  height: 160px;
  margin-bottom: 30px;
  box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.1);
  border-radius: 0px 0px 50px 50px;
}
.header__logo {
  display: flex;
  flex-direction: row;
  width: 305px;
  height: 100px;
  align-items: center;
  margin-left: 30px;
  margin-right: 250px;
}
.logo-img {
  width: 100px;
  height: 100px;
  margin-right: 30px;
  align-items: center;
  background-image: url(img/logo_avega-gif.gif);
  background-repeat: no-repeat;
  background-size: cover;
}
.logo-name {
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 58px;
  color: black;
}
.header__nav {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: auto;
}
.nav__list {
  display: flex;
  align-items: center;
  justify-content: center;
}
.nav__item:not(:last-child) {
  margin-right: 65px;
}
.nav-text {
  font-weight: 700;
  font-size: 30px;
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  background-image: linear-gradient(to right, #ff6a00, #000);
  display: table;
  color: #000;
}
.container {
  max-width: 1800px;
  padding: 0 15px;
  margin: 0 auto;
}
/*шапка*/
.header {
  padding: 10px 0;
  background-color: #fff;
  margin-bottom: 20px;
} 
.navigation {
  display: flex;
  align-items: center;
  gap: 20px;
}
.btn {
  color: #fff;
  background-color: rgb(0, 85, 255);
  padding: 15px 30px;
  border: none;
  border-radius: 0;
  outline: 1px solid transparent;
  outline-offset: 1px;
  transition: background-color .3s ease-in-out, outline-color .3s ease-in-out;
}
.btn:hover {
  background-color: rgb(43, 113, 253);
}
.btn:active {
  background-color: rgb(14, 54, 190);
}
.btn:focus {
  outline-color: rgb(243, 13, 243);
}
/*Контейнер для страниц*/
.page-container {
  padding: 15px;
}
.page {
  background-color: white;
  padding-top: 20px;
  padding-bottom: 20px;
}
.main-title {
  margin-top: 0;
  margin-bottom: 20px;
}
.desc {
  margin-top: 0;
  margin-bottom: 0;
}
.product-list {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}
.product-list__item {
  display: flex;
  flex-direction: column;
  border: 2px solid rgb(184, 184, 184);
  padding: 20px;
}
.product-list__title {
  margin-top: 0;
  font-size: 26px;
  margin-bottom: 20px;
}
.product-list__price {
  font-size: 21px;
  margin-bottom: 15px;
}


/*Каталог*/
.catalogCharters {
  margin-bottom: 70px;
}
.charterTitle {
  margin-bottom: 20px;
}
.charter-link {
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 600;
  font-size: 24px;
  color: #000;
}


/*footer*/
.footer__box {
  display: flex;
  flex-direction: row;
  background-color: #000;
  box-shadow: inset 10px 10px 10px 5px rgb(78, 76, 76);
}
.footer-left {
  display: flex;
  flex-direction: column;
  width: 885px;
  height: 590px;
  padding-top: 30px;
}
.footer-left-title {
  width: 420px;
  margin: 0;
  margin-left: 70px;
  margin-bottom: 30px;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 600;
  font-size: 60px;
  color: #ff6a00;
}
.sert__cont,
.networks-icons {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 10px;
}
.sertificates,
.social-networks {
  width: 450px;
  margin: 0;
  margin-left: 70px;
  margin-bottom: 30px;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 500;
  font-size: 40px;
  color: white;
}
.sert-link,
.youtube-link,
.telegram-link {
  margin: 0;
  margin-left: 15px;
}
.taxes__info {
  display: flex;
  flex-direction: column;
  width: 250px;
  margin-left: 70px;
  margin-top: 10px;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-size: 20px;
  color: white;
}
.footer-right {
  display: flex;
  flex-direction: column;
  max-width: 770px;
  min-height: 590px;
  padding-top: 30px;
}
.footer__title {
  margin-bottom: 25px;
}

.form-request {
  display: flex;
  flex-direction: column;
}

.persons-data {
  display: flex;
  flex-direction: column;
}

.persons__name,
.persons__email,
.persons__tel {
  width: 570px;
  min-height: 60px;
  border-radius: 16px;
  background-color: var(--dark-grey);
  padding: 14px 30px;
  margin-bottom: 25px;
  transition: background-color 0.3s ease-in-out;
}

.persons__comments {
  width: 570px;
  min-height: 90px;
  border-radius: 16px;
  background-color: var(--dark-grey);
  padding: 14px 30px;
  margin-bottom: 32px;
  transition: background-color 0.3s ease-in-out;
}

.persons__name:focus,
.persons__email:focus,
.persons__comments:focus,
.persons__tel:focus {
  background-color: var(--grey);
  outline: none;
}

.persons__name:hover,
.persons__email:hover,
.persons__comments:hover,
.persons__tel:hover {
  background-color: var(--grey);
  outline: none;
}

.persons__name:active,
.persons__email:active,
.persons__comments:active,
.persons__tel:active {
  background-color: var(--grey-light);
  outline: none;
}

.persons__name::placeholder,
.persons__email::placeholder,
.persons__tel::placeholder {
  font-weight: 600;
  font-size: 17px;
  font-style: italic;
  line-height: 32px;
  color: var(--grey-light);
}

.persons__comments::placeholder {
  font-weight: 600;
  font-size: 20px;
  font-style: italic;
  line-height: 32px;
  color: var(--grey-light);
}

.footer-right__request {
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
  width: 570px;
}

.request__btn {
  width: 200px;
  margin: 0;
  margin-right: 22px;
  padding: 17px 17px;
  background-image: linear-gradient(to right, #ff6a00, #000);;
  border-radius: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}

.request__btn:focus {
  background-color: var(--beige-second);
}

.request__btn:hover {
  background-color: var(--beige-second);
}

.request__btn:active {
  background-color: var(--beige-third);
}

.request-btn__text {
  white-space: nowrap;
  font-weight: 600;
  font-size: 17px;
  line-height: 20px;
  color: #fff;
}

