.checkbox-place {
  width: 353px;
  min-height: 32px;
}

.checkbox__text {
  margin: 0;
  font-style: normal;
  font-weight: 600;
  font-size: 15px;
  line-height: 18px;
  color: var(--grey-light);
}

.footer__descr {
  font-weight: 400;
  font-size: 16px;
  line-height: 32px;
  color: var(--grey-light);
}